import React, { Component } from 'react'
import classnames from 'classnames';
import './TestSlider.css';

export class TestSlider extends Component {
    constructor(props) {
        super(props);

        this.state = {};
    }
    render() {

        const { images, startingIndex } = this.props;

        return (

            <div className="sliderWrapper">
                <div className="sliderImgDiv leftImgDiv">
                    {/* <img src={images[startingIndex - 1].src} alt={images[startingIndex - 1].alt} ></img>
                    <img src={images[startingIndex - 1].src} alt={images[startingIndex - 1].alt} className="reflectionImg"></img> */}
                </div>
                <div className="sliderImgDiv centerImgDiv">
                    {/* <img src={images[startingIndex].src} alt={images[startingIndex].alt} ></img>
                    <img src={images[startingIndex].src} alt={images[startingIndex].alt} className="reflectionImg"></img> */}
                </div>
                <div className="sliderImgDiv rightImgDiv">
                    {/* <img src={images[startingIndex + 1].src} alt={images[startingIndex + 1].alt} ></img>
                    <img src={images[startingIndex + 1].src} alt={images[startingIndex + 1].alt} className="reflectionImg"></img> */}
                </div>
            </div>

        )
    }
}

export default TestSlider;