import React, { Component } from 'react'
import classnames from 'classnames';

export class NavbarItem extends Component {
    constructor(props) {
        super(props);

        this.state = {};
    }

    componentDidMount() {

    }

    componentDidUpdate() {
        if (!this.state.showItem && this.props.loadNavbar) {
            const { itemIndex } = this.props;

            var delay = itemIndex * 100 + 1500;

            setTimeout(() => {
                this.setState({ showItem: true });
            }, delay);
        }
    }

    render() {
        const { item } = this.props;
        const { showItem } = this.state;

        return (
            <li className={classnames("navbarItem", {
                "navbarItemShow": showItem
            })}>{item}</li>
        )
    }
}

export default NavbarItem;