import React, { Component } from 'react'
import ImageSliderItem from './ImageSliderItem';
import classnames from 'classnames';
import './ImageSlider.css';

export class ImageSlider extends Component {
    constructor(props) {
        super(props);

        this.state = {
            images: [
                {
                    src: "./images/1/wheat.jpg",
                    alt: "0"
                },
                {
                    src: "./images/1/mountain.jpg",
                    alt: "1"
                },
                {
                    src: "./images/1/turtle.jpg",
                    alt: "2"
                },
                {
                    src: "./images/1/path.jpg",
                    alt: "3"
                },
                {
                    src: "./images/1/hay.jpeg",
                    alt: "4"
                },
            ]
        };

        this.nextImage = this.nextImage.bind(this);
        this.prevImage = this.prevImage.bind(this);
        this.loadBtns = this.loadBtns.bind(this);
    }

    componentDidMount() {
        document.addEventListener('keydown', function (e) {
            if (e.keyCode === 39) this.nextImage();
            else if (e.keyCode === 37) this.prevImage();
        }.bind(this));
    }

    nextImage() {
        const { disableButton } = this.state;
        if (disableButton) return;

        this.setState({ triggerNextImage: true, disableButton: true }, function () {
            this.setState({ triggerNextImage: false });
        }.bind(this));

        setTimeout(() => {
            this.setState({ disableButton: false });
        }, 650);

    }

    prevImage() {
        const { disableButton } = this.state;
        if (disableButton) return;

        this.setState({ triggerPrevImage: true, disableButton: true }, function () {
            this.setState({ triggerPrevImage: false });
        }.bind(this));

        setTimeout(() => {
            this.setState({ disableButton: false });
        }, 650);

    }

    loadBtns() {
        this.setState({ loadBtnsClass: true });
    }

    render() {

        const { images, triggerNextImage, triggerPrevImage, loadBtnsClass } = this.state;

        return (
            <div className="sliderDiv">


                <div className="sliderBtnAndReflectionDiv" onClick={function (e) { this.prevImage() }.bind(this)}>
                    <p className={classnames("sliderBtn sliderPrevImgButton", {
                        "sliderBtnShow": loadBtnsClass
                    })}>{"<"}</p>
                    <p className={classnames("sliderBtnReflection", {
                        "sliderBtnReflectionShow": loadBtnsClass
                    })}>{"<"}</p>
                </div>

                {/* <TestSlider startingIndex={2} images={images} /> */}

                <ImageSliderItem loadBtns={this.loadBtns} topLoaded={this.props.topLoaded} itemQueue={2} size="small" startingIndex={0} images={images} triggerNextImage={triggerNextImage} triggerPrevImage={triggerPrevImage} />
                <ImageSliderItem topLoaded={this.props.topLoaded} itemQueue={1} size="medium" startingIndex={1} images={images} triggerNextImage={triggerNextImage} triggerPrevImage={triggerPrevImage} />
                <ImageSliderItem topLoaded={this.props.topLoaded} itemQueue={0} size="null" startingIndex={2} images={images} triggerNextImage={triggerNextImage} triggerPrevImage={triggerPrevImage} />
                <ImageSliderItem topLoaded={this.props.topLoaded} itemQueue={1} size="medium" startingIndex={3} images={images} triggerNextImage={triggerNextImage} triggerPrevImage={triggerPrevImage} />
                <ImageSliderItem topLoaded={this.props.topLoaded} itemQueue={2} size="small" startingIndex={4} images={images} triggerNextImage={triggerNextImage} triggerPrevImage={triggerPrevImage} />

                <div className="sliderBtnAndReflectionDiv" onClick={function (e) { this.nextImage() }.bind(this)}>
                    <p className={classnames("sliderBtn sliderNextImgButton", {
                        "sliderBtnShow": loadBtnsClass
                    })}>{">"}</p>
                    <p className={classnames("sliderBtnReflection", {
                        "sliderBtnReflectionShow": loadBtnsClass
                    })}>{">"}</p>
                </div>

            </div>
        )
    }
}

export default ImageSlider;