import React, { Component } from 'react'
import "./ImageSliderItem.css";
import classnames from 'classnames';

export class ImageSliderItem extends Component {
    constructor(props) {
        super(props);
        this.state = {
            imageIndex: 0
        };

        this.onClick = this.props.onClick;
        this.nextImage = this.nextImage.bind(this);
        this.prevImage = this.prevImage.bind(this);
        this.loadBtns = this.props.loadBtns;

        // refs
        this.imgRef0 = React.createRef();
        this.imgReflectionRef0 = React.createRef();
        this.imgRef1 = React.createRef();
        this.imgReflectionRef1 = React.createRef();
        this.imgRef2 = React.createRef();
        this.imgReflectionRef2 = React.createRef();
    }


    componentDidMount() {
        this.setState({ imageIndex: this.props.startingIndex });


    }

    componentDidUpdate() {
        const { triggerNextImage, triggerPrevImage } = this.props;
        if (triggerNextImage) {
            this.nextImage();
        } else if (triggerPrevImage) {
            this.prevImage();
        }

        if (!this.state.showSliderItem && this.props.topLoaded) {
            const { itemQueue } = this.props;
            var delay = itemQueue * 500 + 500;
            setTimeout(() => {
                this.setState({ showSliderItem: true });

                if (itemQueue === 2) {
                    var delay2 = (itemQueue - 1) * 500 + 500;
                    setTimeout(() => {
                        if (this.loadBtns) this.loadBtns();
                    }, delay2);
                }

            }, delay);
        }
    }

    nextImage() {
        var { imageIndex } = this.state;
        var { images } = this.props;
        console.log(`next imageIndex: ${imageIndex}`)

        while (this.state.lastTimeoutIdd-- && this.state.lastTimeoutIdd >= 0) {
            // console.log('Timeout ID: ', this.state.lastTimeoutIdd);
            window.clearTimeout(this.state.lastTimeoutIdd); // will do nothing if no timeout with id is present            
        }

        this.setState({ slideLeft: true }, function () {
            setTimeout(() => {
                this.setState({ cancelTransition: true }, function () {

                    if (imageIndex + 1 != images.length) {
                        // if the next pic's index is within bounds (not equal to or greater than the array's size)

                        imageIndex++;
                        // console.log(`imageIndex for img2: ${imageIndex + 1}`)
                        if (imageIndex + 1 >= images.length) {
                            console.log("next if1")
                            this.imgRef2.current.src = images[0].src;
                            this.imgReflectionRef2.current.src = images[0].src;

                            this.imgRef0.current.src = images[imageIndex - 1].src;
                            this.imgReflectionRef0.current.src = images[imageIndex - 1].src;
                        } else {
                            console.log("next else1");
                            this.imgRef0.current.src = images[imageIndex - 1].src;
                            this.imgReflectionRef0.current.src = images[imageIndex - 1].src;

                            this.imgRef2.current.src = images[imageIndex + 1].src;
                            this.imgReflectionRef2.current.src = images[imageIndex + 1].src;
                        }
                        this.imgRef1.current.src = images[imageIndex].src;
                        this.imgReflectionRef1.current.src = images[imageIndex].src;
                        this.setState({ imageIndex });
                    }
                    else {
                        console.log("next restart");
                        this.imgRef2.current.src = images[1].src;
                        this.imgReflectionRef2.current.src = images[1].src;

                        this.imgRef1.current.src = images[0].src;
                        this.imgReflectionRef1.current.src = images[0].src;

                        this.imgRef0.current.src = images[images.length - 1].src;
                        this.imgReflectionRef0.current.src = images[images.length - 1].src;
                        this.setState({ imageIndex: 0 });
                    }

                    this.setState({ slideLeft: false }, function () {
                        var lastTimeoutId = setTimeout(() => {
                            this.setState({ cancelTransition: false });
                        }, 100);

                        this.setState({ lastTimeoutIdd: lastTimeoutId });
                    }.bind(this));
                }.bind(this));
            }, 500);
        }.bind(this));
    }

    prevImage() {
        var { imageIndex } = this.state;
        var { images } = this.props;
        // console.log(`imageIndex: ${imageIndex}`)

        while (this.state.lastTimeoutIdd-- && this.state.lastTimeoutIdd >= 0) {
            // console.log('Timeout ID: ', this.state.lastTimeoutIdd);
            window.clearTimeout(this.state.lastTimeoutIdd); // will do nothing if no timeout with id is present            
        }

        this.setState({ slideRight: true }, function () {
            setTimeout(() => {
                this.setState({ cancelTransition: true }, function () {

                    if (imageIndex - 1 >= 0) {
                        console.log("prev if");
                        // if the previous pic's index is within bounds (not less than the array's 0)
                        imageIndex--;

                        console.log('prev imageindex:', imageIndex);
                        console.log('prev imageindex-1:', imageIndex - 1);

                        if (imageIndex === 0) {
                            console.log("prev 2if");
                            this.imgRef0.current.src = images[images.length - 1].src;
                            this.imgReflectionRef0.current.src = images[images.length - 1].src;

                            this.imgRef1.current.src = images[0].src;
                            this.imgReflectionRef1.current.src = images[0].src;

                            this.imgRef2.current.src = images[imageIndex + 1].src;
                            this.imgReflectionRef2.current.src = images[imageIndex + 1].src;

                            this.setState({ imageIndex: 0 });
                        }
                        else {
                            console.log("prev else1");
                            this.imgRef0.current.src = images[imageIndex - 1].src;
                            this.imgReflectionRef0.current.src = images[imageIndex - 1].src;

                            this.imgRef1.current.src = images[imageIndex].src;
                            this.imgReflectionRef1.current.src = images[imageIndex].src;

                            this.imgRef2.current.src = images[imageIndex + 1].src;
                            this.imgReflectionRef2.current.src = images[imageIndex + 1].src;

                            this.setState({ imageIndex });
                        }

                    }
                    else {
                        imageIndex = images.length - 1;
                        console.log("prev else2");
                        this.imgRef0.current.src = images[images.length - 2].src;
                        this.imgReflectionRef0.current.src = images[images.length - 2].src;

                        this.imgRef1.current.src = images[images.length - 1].src;
                        this.imgReflectionRef1.current.src = images[images.length - 1].src;

                        if (imageIndex + 1 >= images.length) {
                            this.imgRef2.current.src = images[0].src;
                            this.imgReflectionRef2.current.src = images[0].src;
                        }

                        this.setState({ imageIndex });

                    }


                    this.setState({ slideRight: false }, function () {
                        var lastTimeoutId = setTimeout(() => {
                            this.setState({ cancelTransition: false });
                        }, 100);

                        this.setState({ lastTimeoutIdd: lastTimeoutId });
                    }.bind(this));
                }.bind(this));
            }, 500);
        }.bind(this));
    }

    render() {

        const { slideLeft, slideRight, cancelTransition, showSliderItem } = this.state;
        var { images, startingIndex, size } = this.props;

        var nextIndex = startingIndex;
        if (nextIndex === images.length - 1) {
            nextIndex = 0;
        } else {
            nextIndex++;
        }

        var prevIndex = startingIndex;
        if (prevIndex === 0) {
            prevIndex = images.length - 1;
        } else {
            prevIndex--;
        }

        return (
            <div className={classnames("imageSliderItemWrapper", {
                "imageSliderItemWrapperMedium": size === "medium"
                , "imageSliderItemWrapperSmall": size === "small"
            })}>

                <div className={classnames("imageSliderItem", {
                    "imageSliderItemMedium": size === "medium"
                    , "imageSliderItemSmall": size === "small"
                    , "imageSliderItemShow": showSliderItem
                })}>
                    <div className={classnames("imgAndReflectionDiv", {
                        "cancelTransition": cancelTransition
                        , "slideLeft": slideLeft
                        , "slideRight": slideRight
                        , "imgAndReflectionDivMedium": size === "medium"
                        , "imgAndReflectionDivSmall": size === "small"
                    })}>
                        <img className={classnames("mainImg", {
                            "medium": size === "medium"
                            , "small": size === "small"
                        })} ref={this.imgRef0} src={images[prevIndex].src} alt={images[prevIndex].alt}></img>
                        <div className="imgReflectionWrapper">
                            <img className={classnames("imgReflection", {
                                "imgReflectionMedium": size === "medium"
                                , "imgReflectionSmall": size === "small"
                            })}
                                ref={this.imgReflectionRef0} src={images[prevIndex].src} alt={images[prevIndex].alt}></img>
                        </div>

                    </div>

                    <div className={classnames("imgAndReflectionDiv", {
                        "cancelTransition": cancelTransition
                        , "slideLeft": slideLeft
                        , "slideRight": slideRight
                        , "imgAndReflectionDivMedium": size === "medium"
                        , "imgAndReflectionDivSmall": size === "small"
                    })}>
                        <img className={classnames("mainImg", {
                            "medium": size === "medium"
                            , "small": size === "small"
                        })} ref={this.imgRef1} src={images[startingIndex].src} alt={images[startingIndex].alt}></img>
                        <div className="imgReflectionWrapper">
                            <img className={classnames("imgReflection", {
                                "imgReflectionMedium": size === "medium"
                                , "imgReflectionSmall": size === "small"
                            })}
                                ref={this.imgReflectionRef1} src={images[startingIndex].src} alt={images[startingIndex].alt}></img>
                        </div>

                    </div>

                    <div className={classnames("imgAndReflectionDiv", {
                        "cancelTransition": cancelTransition
                        , "slideLeft": slideLeft
                        , "slideRight": slideRight
                        , "imgAndReflectionDivMedium": size === "medium"
                        , "imgAndReflectionDivSmall": size === "small"
                    })}>
                        <img className={classnames("mainImg", {
                            "medium": size
                        })} ref={this.imgRef2} src={images[nextIndex].src} alt={images[nextIndex].alt}></img>
                        <div className="imgReflectionWrapper">
                            <img className={classnames("imgReflection", {
                                "imgReflectionMedium": size === "medium"
                                , "imgReflectionSmall": size === "small"
                            })}
                                ref={this.imgReflectionRef2} src={images[nextIndex].src} alt={images[nextIndex].alt}></img>
                        </div>
                    </div>


                </div>

            </div >
        )
    }
}

export default ImageSliderItem;