import React, { Component } from 'react'
import classnames from 'classnames';
import './Logo.css';
import LogoLetter from './LogoLetter';

export class Logo extends Component {
    constructor(props) {
        super(props);

        this.state = {
            photographyLogoHide: false
        }
    }

    componentDidMount() {
        setTimeout(() => {
            this.setState({ photographyLogoHide: true });
        }, 4000);

        setTimeout(() => {
            this.setState({ showBluePrintLogo: true });
        }, 3000);

        setTimeout(() => {
            this.setState({ logoDivBar: true });
        }, 2700);
    }

    render() {

        const { logoDivBar, photographyLogoHide, showBluePrintLogo } = this.state;

        return (
            <div className={classnames("logoDiv", {
                "logoDivBar": logoDivBar
            })}>
                <img src="./images/bluePrintProductions.png" alt="bp" className={classnames("blueprintProductionsImg", {
                    "blueprintProductionsImgShow": showBluePrintLogo
                })}></img>

                <div className="logoLetterLists">

                    <ul className={classnames("logoLetterList gregLogo", {
                        // 'gregLogoDark': darkTheme
                    })}>
                        {"Greg".split("").map(function (letter, index) {
                            return <LogoLetter letter={letter} itemIndex={index} letterType="left" />
                        })}
                    </ul>

                    <ul className="logoLetterList lewisLogo">
                        {"Lewis".split("").map(function (letter, index) {
                            return <LogoLetter letter={letter} itemIndex={index} letterType="leftLewis" />
                        })}
                    </ul>

                    <ul className={classnames("logoLetterList photographyLogo", {
                        "photographyLogoHide": photographyLogoHide
                    })}>
                        {"Photography".split("").map(function (letter, index) {
                            return <LogoLetter letter={letter} itemIndex={index} letterType="right" />
                        })}
                    </ul>

                </div>

            </div >
        )
    }
}

export default Logo;