import React, { Component } from 'react'
import "./Navbar.css";
import NavbarItem from './NavbarItem';

export class Navbar extends Component {
    constructor(props) {
        super(props);

        this.state = {
            navbarItems: [
                "Home",
                "About",
                "Contact",
                "Rates",
                "Shop",
                "Videos",
                "Collaboration"
            ]
        }
    }

    componentDidMount() {
        setTimeout(() => {
            this.setState({ loadNavbar: true });
        }, 2000);
    }

    render() {

        const { navbarItems } = this.state;

        return (
            <div className="navbarDiv">
                <ul className="navbarItemsList">
                    {navbarItems.map(function (item, index) {
                        return <NavbarItem loadNavbar={this.state.loadNavbar} item={item} itemIndex={index} />
                    }.bind(this))}
                </ul>
            </div>
        )
    }
}

export default Navbar;