import React, { Component } from 'react'
import classnames from 'classnames';
import "./LogoLetter.css";

export class LogoLetter extends Component {
    constructor(props) {
        super(props)

        this.state = {
            showLetter: false
            , moveFirstLeft: false
        };
    }

    componentDidMount() {
        const { letter, letterType, itemIndex } = this.props;

        var delay = itemIndex * 80 + 2000;
        var delayLewis = itemIndex * 80 + 2100;

        var delayRight = (11 - itemIndex) * 50 + 2000;

        // first show first letters immediately
        if (itemIndex === 0 || letter === "L") {
            setTimeout(() => {
                this.setState({ showLetter: true }, function () {
                    setTimeout(() => {
                        this.setState({ moveFirstLeft: true });
                    }, 800);
                });
            }, 1000);
        }


        if (letterType === "left") {
            setTimeout(() => {
                this.setState({ showLetter: true });
            }, delay);
        }
        else if (letterType === "leftLewis") {
            setTimeout(() => {
                this.setState({ showLetter: true });
            }, delayLewis);
        }
        else if (letterType === "right") {
            setTimeout(() => {
                this.setState({ showLetter: true });
            }, delayRight);
        }

    }

    render() {

        const { letter } = this.props;
        const { showLetter, moveFirstLeft } = this.state;
        return (
            <li className={classnames("", {
                "showLetter": showLetter
                , "moveFirstLeft": moveFirstLeft
            })}>
                <h1>{letter}</h1>
            </li>
        )
    }
}

export default LogoLetter;